.fuhsa-1-icon5 {
  position: relative;
  width: 248px;
  height: 50px;
  object-fit: cover;
}
.fuhsaicon5 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.fuhsaprofileboxnoticon5 {
  border-radius: var(--br-981xl);
  overflow: hidden;
  flex-shrink: 0;
}
.avatar-icon5,
.fuhsaprofileboxnoticon5 {
  position: relative;
  width: 40px;
  height: 40px;
}
.chevron-down-icon5 {
  position: relative;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.fuhsaprofileboximg5 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
  cursor: pointer;
}
.fuhsaprofilebox16,
.fuhsaprofilebox7 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
.fuhsaprofilebox16 {
  gap: var(--gap-5xl);
}
.fuhsaprofilebox7 {
  flex: 1;
  height: 48px;
}
.navbarlogintext5 {
  position: relative;
}
.fuhsanavbar5,
.navbarlogin5 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.navbarlogin5 {
  border-radius: var(--br-7xs);
  background-color: var(--color-rosybrown);
  overflow: hidden;
  padding: var(--padding-6xs) var(--padding-6xl);
  cursor: pointer;
}
.fuhsaappliedboxbutton:hover,
.navbarlogin5:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.fuhsanavbar5 {
  align-self: stretch;
  gap: var(--gap-3xs);
}
.fuhsaappliedboxgood-icon {
  position: relative;
  width: 96px;
  height: 96px;
  object-fit: cover;
}
.fuhsaappliedboxcongratulations {
  align-self: stretch;
  position: relative;
  font-size: var(--h2-bold-32-size);
  font-weight: 700;
}
.fuhsaappliedboxkeep {
  align-self: stretch;
  color: var(--logo-black);
}
.close,
.fuhsaappliedboxcheck,
.fuhsaappliedboxkeep {
  position: relative;
  line-height: 150%;
}
.fuhsaappliedboxcheck {
  align-self: stretch;
  color: var(--color-crimson);
}
.close {
  font-size: var(--p1-regular-16-size);
  font-family: var(--p1-regular-16);
  color: var(--gray-1);
  text-align: center;
}
.fuhsaappliedboxbutton {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) 27px;
  background-color: var(--royal-blue);
  border-radius: var(--br-7xs);
  width: 589px;
  height: 44px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.fuhsaappliedboxprintacknowlere {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-smi);
  line-height: 150%;
  color: #64748b;
}
.applied,
.fuhsaappliedbox {
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}
.fuhsaappliedbox {
  align-self: stretch;
  border-radius: var(--br-5xs);
  height: 521px;
  justify-content: flex-start;
  padding: 40px 306px 0;
  gap: var(--gap-3xs);
  font-size: var(--p1-regular-16-size);
  color: var(--royal-blue);
}
.applied {
  position: relative;
  width: 100%;
  overflow: hidden;
  justify-content: center;
  padding: var(--padding-31xl) var(--padding-50xl);
  gap: 13px;
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--white);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 1200px) {
  .fuhsa-1-icon5 {
    width: 248px;
  }
  .fuhsaappliedboxbutton {
    width: 428px;
  }
}
@media screen and (max-width: 960px) {
  .fuhsa-1-icon5 {
    width: 178px;
    height: 27px;
  }
  .avatar-icon5,
  .fuhsaprofileboxnoticon5 {
    width: 30px;
    height: 30px;
  }
  .chevron-down-icon5 {
    width: 14px;
    height: 14px;
  }
  .fuhsaprofileboximg5 {
    gap: var(--gap-9xs);
  }
  .fuhsaprofilebox16 {
    gap: var(--gap-5xs);
  }
  .navbarlogintext5 {
    font-size: var(--font-size-3xs);
  }
  .fuhsaappliedboxbutton {
    width: 328px;
  }
  .fuhsaappliedboxprintacknowlere {
    font-size: 11px;
  }
  .applied,
  .fuhsaappliedbox {
    padding-left: var(--padding-51xl);
    padding-right: var(--padding-51xl);
    box-sizing: border-box;
  }
  .applied {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
  }
}
@media screen and (max-width: 420px) {
  .fuhsa-1-icon5 {
    width: 158px;
  }
  .fuhsaprofileboxnoticon5 {
    width: 30px;
    height: 30px;
  }
  .avatar-icon5 {
    width: 25px;
    height: 25px;
  }
  .chevron-down-icon5 {
    width: 13px;
    height: 13px;
  }
  .fuhsaprofilebox16 {
    gap: var(--gap-8xs);
  }
  .fuhsanavbar5 {
    gap: var(--gap-0);
    padding-right: 0;
    box-sizing: border-box;
  }
  .fuhsaappliedboxgood-icon {
    width: 66px;
    height: 66px;
  }
  .fuhsaappliedboxcongratulations {
    font-size: 17px;
  }
  .fuhsaappliedboxkeep {
    font-size: var(--font-size-xs);
  }
  .fuhsaappliedboxcheck {
    font-size: var(--font-size-3xs);
  }
  .close {
    font-size: var(--font-size-xs);
  }
  .fuhsaappliedboxbutton {
    width: 228px;
  }
  .fuhsaappliedboxprintacknowlere {
    font-size: var(--font-size-4xs);
  }
  .fuhsaappliedbox {
    padding-left: var(--padding-16xl);
    padding-right: var(--padding-16xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .close {
    font-size: var(--font-size-sm);
  }
}
