.applyconfirmbox1upload-icon {
  position: relative;
  border-radius: 50%;
  width: 95px;
  height: 95px;
  object-fit: cover;
}
.applyconfirmbox2,
.upload {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.applyconfirmbox2 {
  align-self: stretch;
}
.applyconfirmroleboxtext {
  flex: 1;
  position: relative;
}
.applyconfirmrolebox {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs) 0;
  font-size: var(--font-size-xl);
  color: var(--color-firebrick);
}
.applyconfirmdepartment {
  align-self: stretch;
  position: relative;
}
.applyconfirmcategorytext {
  position: relative;
}
.applyconfirmcategory {
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-10xs) 0;
}
.applyconfirmprofiletotext {
  flex: 1;
  position: relative;
}
.applyconfirmprofileto {
  width: 233px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 0 var(--padding-5xs);
  box-sizing: border-box;
  font-size: var(--font-size-xs);
  color: var(--color-gray-100);
}
.applyconfirmcontinuetext {
  position: relative;
  font-weight: 600;
}
.applyconfirmcontinue {
  border-radius: var(--br-3xs);
  background-color: var(--color-firebrick);
  overflow: hidden;
  padding: var(--padding-4xs) var(--padding-7xl);
  cursor: pointer;
}
.applyconfirmcontinue:hover,
.applyconfirmedit:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.applyconfirmcontinue,
.applyconfirmedit,
.applyconfirmprofilebuttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.applyconfirmedit {
  border-radius: var(--br-3xs);
  background-color: var(--color-royalblue);
  overflow: hidden;
  padding: var(--padding-4xs) var(--padding-7xl);
}
.applyconfirmprofilebuttons {
  align-self: stretch;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--p1-regular-16-size);
  color: var(--white);
}
.applyconfirm,
.applyconfirmbox1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.applyconfirmbox1 {
  gap: var(--gap-5xs);
}
.applyconfirm {
  border-radius: 17px;
  background-color: var(--white);
  width: 501px;
  overflow: hidden;
  padding: var(--padding-17xl) var(--padding-40xl);
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100%;
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--color-black);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 960px) {
  .applyconfirmbox1upload-icon,
  .upload {
    width: 84px;
    height: 84px;
  }
  .upload {
    height: 88px;
  }
  .applyconfirmroleboxtext {
    font-size: var(--font-size-sm);
  }
  .applyconfirmcategorytext,
  .applyconfirmdepartment,
  .applyconfirmprofiletotext {
    font-size: var(--font-size-xs);
  }
  .applyconfirmcontinuetext {
    font-size: var(--font-size-3xs);
  }
  .applyconfirmbox1,
  .applyconfirmcontinue {
    padding-left: var(--padding-3xs);
    padding-right: var(--padding-3xs);
    box-sizing: border-box;
  }
  .applyconfirmbox1 {
    padding-left: var(--padding-lgi);
    padding-right: var(--padding-lgi);
  }
  .applyconfirm {
    width: 350px;
  }
}
@media screen and (max-width: 420px) {
  .applyconfirmcategorytext,
  .applyconfirmdepartment,
  .applyconfirmprofiletotext {
    font-size: var(--font-size-3xs);
  }
}
