.profile-updated {
  align-self: stretch;
  position: relative;
}
.ok-icon {
  position: relative;
  width: 96px;
  height: 96px;
  object-fit: cover;
}
.profileupdatedmark {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.backtoprofileupddated {
  position: relative;
  cursor: pointer;
}
.backtoprofilebox,
.backtoprofilebuttonbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.backtoprofilebuttonbox {
  flex: 1;
}
.backtoprofilebox {
  align-self: stretch;
  color: var(--color-firebrick);
}
.profileupdated,
.profileupdatedbox2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.profileupdatedbox2 {
  align-self: stretch;
  gap: var(--gap-3xs);
}
.profileupdated {
  border-radius: var(--br-xs);
  background-color: var(--white);
  width: 501px;
  overflow: hidden;
  padding: var(--padding-17xl) var(--padding-40xl);
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100%;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-royalblue);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 960px) {
  .backtoprofileupddated,
  .profile-updated {
    font-size: var(--font-size-sm);
  }
  .profileupdatedbox2 {
    padding-left: var(--padding-lgi);
    padding-right: var(--padding-lgi);
    box-sizing: border-box;
  }
  .profileupdated {
    width: 350px;
  }
}
@media screen and (max-width: 420px) {
  .profile-updated {
    font-size: var(--font-size-3xs);
  }
  .ok-icon {
    width: 66px;
    height: 66px;
  }
  .backtoprofileupddated {
    font-size: var(--font-size-3xs);
  }
}
