.fuhsa-1-icon {
  position: relative;
  width: 248px;
  height: 50px;
  object-fit: cover;
}
.fuhsaicon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.fuhsaprofileboxnoticon {
  border-radius: var(--br-981xl);
  overflow: hidden;
  flex-shrink: 0;
}
.avatar-icon,
.fuhsaprofileboxnoticon {
  position: relative;
  width: 40px;
  height: 40px;
}
.chevron-down-icon {
  position: relative;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.fuhsaprofileboximg {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
  cursor: pointer;
}
.fuhsaprofilebox,
.fuhsaprofilebox1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
.fuhsaprofilebox1 {
  gap: var(--gap-5xl);
}
.fuhsaprofilebox {
  flex: 1;
  height: 48px;
}
.navbarlogintext {
  position: relative;
}
.fuhsanavbar,
.navbarlogin {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.navbarlogin {
  border-radius: var(--br-7xs);
  background-color: var(--color-rosybrown);
  overflow: hidden;
  padding: var(--padding-6xs) var(--padding-6xl);
  cursor: pointer;
}
.buttonxxlarge:hover,
.navbarlogin:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.fuhsanavbar {
  align-self: stretch;
  gap: var(--gap-3xs);
}
.profileformsboxinputtext {
  align-self: stretch;
  position: relative;
  line-height: 150%;
}
.fuhsasignupinput,
.login1 {
  font-size: var(--p1-regular-16-size);
  font-family: var(--p1-regular-16);
}
.fuhsasignupinput {
  border: 0;
  background-color: var(--white);
  align-self: stretch;
  border-radius: var(--br-7xs);
  box-shadow: var(--shadow-1);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-3xs) var(--padding-xs);
}
.login1 {
  flex: 1;
  position: relative;
  line-height: 150%;
  color: var(--gray-1);
  text-align: center;
}
.buttonxxlarge {
  cursor: pointer;
  border: 0;
  padding: var(--padding-3xs) 0;
  background-color: var(--color-firebrick);
  align-self: stretch;
  border-radius: var(--br-7xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.dont-have-an {
  font-size: var(--font-size-smi);
  text-decoration: underline;
}
.dont-have-an,
.forgot-password,
.wrong-password {
  align-self: stretch;
  position: relative;
}
.forgot-password {
  text-decoration: underline;
  color: var(--color-gray-100);
}
.wrong-password {
  color: var(--color-crimson);
}
.forgotframefuhsa {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-7xs);
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--color-dimgray);
}
.fuhsasignupbox {
  border-radius: var(--br-5xs);
  background-color: var(--white);
  width: 580px;
  overflow: hidden;
  padding: 0 var(--padding-mini);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.fuhsasignupbox,
.login,
.navbarmainsignupbody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.navbarmainsignupbody {
  align-self: stretch;
  text-align: left;
  font-size: var(--p1-regular-16-size);
  color: var(--logo-black);
}
.login {
  position: relative;
  background-color: var(--white);
  width: 100%;
  overflow: hidden;
  padding: var(--padding-34xl) var(--padding-50xl);
  box-sizing: border-box;
  gap: var(--gap-30xl);
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--white);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 1200px) {
  .fuhsa-1-icon {
    width: 248px;
  }
}
@media screen and (max-width: 960px) {
  .fuhsa-1-icon {
    width: 178px;
    height: 27px;
  }
  .avatar-icon,
  .fuhsaprofileboxnoticon {
    width: 30px;
    height: 30px;
  }
  .chevron-down-icon {
    width: 14px;
    height: 14px;
  }
  .fuhsaprofileboximg {
    gap: var(--gap-9xs);
  }
  .fuhsaprofilebox1 {
    gap: var(--gap-5xs);
  }
  .navbarlogintext {
    font-size: var(--font-size-3xs);
  }
  .login1 {
    font-size: var(--font-size-sm);
  }
  .dont-have-an,
  .forgot-password,
  .wrong-password {
    font-size: var(--font-size-xs);
  }
  .fuhsasignupbox {
    width: 420px;
  }
  .login {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .fuhsa-1-icon {
    width: 158px;
  }
  .fuhsaprofileboxnoticon {
    width: 30px;
    height: 30px;
  }
  .avatar-icon {
    width: 25px;
    height: 25px;
  }
  .chevron-down-icon {
    width: 13px;
    height: 13px;
  }
  .fuhsaprofilebox1 {
    gap: var(--gap-8xs);
  }
  .fuhsanavbar {
    gap: var(--gap-0);
    padding-right: 0;
    box-sizing: border-box;
  }
  .profileformsboxinputtext {
    font-size: var(--font-size-mini);
  }
  .login1 {
    font-size: var(--font-size-sm);
    text-align: center;
  }
  .dont-have-an,
  .forgot-password,
  .wrong-password {
    font-size: var(--font-size-4xs);
  }
  .fuhsasignupbox {
    width: 90%;
  }
  .login {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
